<template>
    <div class='progress'>
        <el-progress type="circle" :percentage="percentage" color="#20C6BA" :width="width" :stroke-width="stroke" v-if="status === 'waiting'"></el-progress>
        <i class="iconfont icon-icon_success" v-if="status === 'success'"></i>
        <i class="iconfont icon-icon_wrong" v-if="status === 'error'"></i>
        <div class='text'>{{text}}</div>
    </div>
</template>
<script>
export default {
    data () {
        return {
            width: 76,
            stroke: 4
        }
    },
    props: ['percentage', 'show', 'text', 'status']
}
</script>
<style lang="scss" scoped>
.progress{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background: #fff;
    width: 1.8rem;
    height: 1.8rem;
    z-index: 100;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column;
    font-family: PingFangSC-Medium;
    font-size: .14rem;
    color: #263238;
    background: #FFFFFF;
    box-shadow: 0 0 20px 0 rgba(0,0,0,0.10);
    border-radius: 8px;
    .text{
        margin-top: .2rem;
    }
    .icon-icon_success{
        font-size: .8rem;
        &:before{
            color: #52C41A;
        }
    }
    .icon-icon_wrong{
        font-size: .8rem;
        &:before{
            color: #FC7563;
        } 
    }
}
</style>





