<template>
    <div class='member'>
        <div class='top'>
           <span class='fundInfo' @click='goback()'>会员管理</span>
           <img src='../../assets/images/icon_nav_arrow.png'/>
           <span class='branch'>导入会员卡</span>
       </div>
       <div class='first'>
           <div class='title'>第一步</div>
           <div class='content'>
               <div class='text'>{{text}}</div>
               <div class='img' @click="getAddress">
                   <img src='../../assets/images/icon_excel.png'/>
                   <div>会员模板.xlsx</div>
               </div>
               <div class='tips'>
                   <div>注意:</div>
                   <div>1.数据模板Excel文件中带*号的内容为必填项，缺填记录将不会导入。</div>
                   <div>2.数据模板Excel文件中，表头带有注意事项与格式要求，请按照格式要求进行填写。</div>
               </div>
           </div>
       </div>
       <div class='first'>
           <div class='title'>第二步</div>
           <div class='content'>
               <div class='text'>{{sectext}}</div>
               <div class='btn'>
                   <!-- <i class="iconfont icon-btn_upload">上传文件</i> -->
                   <el-upload
                    class="upload-demo"
                    action="https://jsonplaceholder.typicode.com/posts/"
                    :before-upload="beforeUpload"
                    :http-request='upload'
                    :file-list="fileList">
                    <el-button size="small" type="primary"><i class="iconfont icon-btn_upload">上传文件</i></el-button>
                   </el-upload>
                   <!-- <input id="fileToUpload" type="file" name="file" @change="getFile($event)"> -->
               </div>
               <div class='tips'>
                   <div>注意:</div>
                   <div>1.手机号重复记录的只会导入成一个会员记录。</div>
               </div>
           </div>
       </div>
       <div v-if='percent'>
           <gress :percentage="percentage" :text="gresstext" :status="status"></gress>
       </div>
    </div>
</template>
<script>
import {upload, uploadImport, downloadAddress} from '../../services/service.js'
import gress from '../../components/progress'
import axios from 'axios'
import { setTimeout } from 'timers';
import { isApiResponseOk } from '../../utils/com.js';
export default {
    data () {
        return {
            text: '下载导入文件模板，并将会员信息按要求填写到模板文件中。',
            sectext: '将填好的模板上传到系统中。',
            fileList: [],
            percentage: 0,
            percent: false,
            gresstext: '导入中...',
            status: 'waiting'
        }
    },
    components: {
        gress: gress
    },
    methods: {
        // 上传execl文件
        async upload (item) {
            this.percent = true
            let formData = new FormData()
            formData.append('file', item.file)
            const res = await uploadImport(formData, {type: 'json', responseType: 'arraybuffer',
               onUploadProgress: progressEvent => {
                    var complete = (progressEvent.loaded / progressEvent.total * 100 | 0);
                    this.percentage = complete
               }
            })
            if (isApiResponseOk(res)) {
                this.gresstext = '导入完成！'
                this.status = 'success'
                setTimeout(_=>{
                    this.percent = false
                }, 2000)
            } else {
                this.gresstext = '导入失败！'
                this.status = 'error'
                setTimeout(_=>{
                    this.percent = false
                }, 2000)
            }
        },
        // 上传之前判断
        beforeUpload (file) {
            let fileName = file.name
            let arr = fileName.split('.')
            let fileType = arr[arr.length - 1]
            console.log('文件类型', fileType)
            const isLt2M = file.size / 1024 / 1024 < 10 // 文件大小小于10m
            if (fileType !== 'xlsx' && fileType !== 'xls') {
                this.$message({
                    message: '请上传execl文件',
                    type: 'warning'
                })
                return false
            } else if (!isLt2M) {
                this.$message({
                    message: '上传文件大小不能超过10M',
                    type: 'warning'
                })
            } else {
                return true
            }
        },
        downFile (url) {
            var elemIF = document.createElement("iframe");   
            elemIF.src = url;   
            elemIF.style.display = "none";   
            document.body.appendChild(elemIF);
        },
        // 获取文件下载链接
        async getAddress () {
            let data = {
                templateTypeEnums: ['USER_INFO']
            }
            const res = await downloadAddress (data) 
            if (isApiResponseOk(res)) {
                if (res.data && res.data.typeMap) {
                    let url = res.data.typeMap['USER_INFO']
                    url = url.replace('http:', 'https:')
                    this.downFile(url)
                    this.$message({
                        message: '下载成功',
                        type: 'success'
                    })
                }
            } else {
            }

        },
        goback () {
            this.$router.go(-1)
        }
    }
}
</script>
<style lang="scss" scoped>
.member{
    width: 100%;
    .top{
        display: flex;
        align-items: flex-end;
       .fundInfo{
            font-size: .17rem;
            color: #8597A1;
            cursor: pointer;
       }
       img{
           margin-right: .16rem;
           margin-left: .13rem;
       }
       .branch{
           font-family: PingFangSC-Semibold;
           font-size: .21rem;
           color: #263238;
       } 
    }
    .first{
        background: #fff;
        margin-top: .31rem;
        padding-bottom: .3rem;
        .title{
            font-family: PingFangSC-Semibold;
            font-size: 20px;
            color: #263238;
            letter-spacing: 0;
            line-height: .59rem;; 
            height: .59rem;
            border-bottom: 1px solid rgba(216,216,216,.3);
            margin-left: .3rem;
        }
        .content{
            margin-left: .3rem;
            .text{
                font-family: PingFangSC-Regular;
                font-size: .16rem;
                color: #263238;
                letter-spacing: 0;
                line-height: .16rem;
                margin-top: .3rem;
            }
            .img{
                width: 1.26rem;
                height: 1.26rem;
                text-align: center;
                border: 1px solid rgba(192,196,204,.3);
                border-radius: .02rem;
                margin-top: .2rem;
                display: flex;
                flex-flow: column;
                align-items: center;
                justify-content: center;
                font-family: PingFangSC-Regular;
                font-size: .12rem;
                color: #263238;
                letter-spacing: 0;
                cursor: pointer;
                img{
                    width: .5rem;
                    height: .5rem;
                    margin-bottom: .2rem;
                }
            }
            .tips{
                font-family: PingFangSC-Regular;
                font-size: .12rem;
                color: #8597A1;
                letter-spacing: 0;
                margin-top: .2rem;
                line-height: .2rem;
            }
            .el-button--primary{
                background: #20C6BA;
                width: 1.36rem;
                height: .44rem;
                text-align: center;
                font-size: .16rem;
                color: #FFFFFF;
                margin-top: .2rem;
                cursor: pointer;
                border: none;
                .icon-btn_upload:before{
                    margin-right: .12rem;
                }
            }
        }
    }
}
</style>




